.Content-defaults {
    background-color: #fffbc4;
}

.Content-grid {
    display: grid;
    grid-template-columns: 1fr;
    grid-template-rows : auto;
    grid-gap: 4em;
}

/* typeface */
.Content-h3 {
    font-family: 'Shrikhand', cursive;
    letter-spacing: 1px;
  }

.Content-p {
    font-family: 'Raleway', sans-serif;
    line-height: 1.35em;
}


